import {
  AfterViewInit, Component, ContentChild, ContentChildren, EventEmitter, Input, OnInit, Output, QueryList,
  ViewChild
} from '@angular/core';
import {HttpClient, HttpParams} from '@angular/common/http';

import {
  CellClickEvent,
  ColumnBase, DataStateChangeEvent, DetailTemplateDirective, GridComponent, GridDataResult, RowClassFn, ScrollMode, SelectableSettings,
  ToolbarTemplateDirective
} from '@progress/kendo-angular-grid';
import {process, State} from '@progress/kendo-data-query';

import {Observable} from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import {RowSelectedFn} from '@progress/kendo-angular-grid/dist/npm/rendering/common/row-class';

@Component({
  selector: 'eg-grid',
  template: `<kendo-grid

  [data]="gridView"

  [skip]="state.skip"
  [pageSize]="pageSize"
  [group]="state.group"
  [filter]="state.filter"
  [sort]="state.sort"

  [sortable]="sortable"
  [filterable]="filterable"
  [groupable]="groupable"
  [pageable]="pageable"

  [resizable]="resizable"
  [reorderable]="reorderable"
  [selectable]="selectable"

  [height]="height"
  [navigable]="navigable"
  [scrollable]="scrollable"

  (dataStateChange)="dataStateChange($event)"

  (cellClick)="event('cellClick')($event)"

>

  <kendo-grid-checkbox-column *ngIf="selectable && columns && columns.length > 0" #checkboxColumn showSelectAll="true"></kendo-grid-checkbox-column>

  <!--<ng-content select="[kendoGridToolbarTemplate]" ngProjectAs="[kendoGridToolbarTemplate]"></ng-content>-->

  <!--<ng-content select="kendo-grid-column" ngProjectAs="kendo-grid-column"></ng-content>-->

</kendo-grid>
`
})
export class EgGridComponent implements AfterViewInit, OnInit {

  // Inputs:

  /**
   * Adatok tömbje.
   */
  @Input() data = [];

  /**
   * Backendes adatok elérése. Ha meg van adva, innen kerülnek lekérésre az adatok a data helyett.
   */
  @Input() dataUrl: string;

  /**
   * Rendezhetőség.
   */
  @Input() sortable = true;

  /**
   * Szűrhetőség.
   */
  @Input() filterable = true;

  /**
   * Csoportosítás.
   */
  @Input() groupable = true;

  /**
   * Lapozás.
   */
  @Input() pageable = true;

  /**
   * Lapméret.
   */
  @Input() pageSize = 10;

  /**
   * Méretezhetőség.
   */
  @Input() resizable = false;

  /**
   * Újrarendezhetőség.
   */
  @Input() reorderable = false;

  /**
   * Kiválaszhatóság.
   */
  @Input() selectable: boolean | SelectableSettings = false;

  /**
   * Grid magassága.
   */
  @Input() height: number;

  /**
   * Billentyűzettel való irányítás.
   */
  @Input() navigable = false;

  /**
   * Görgetés.
   */
  @Input() scrollable: ScrollMode;

  /**
   * Sorra vonatkozó classt állt be a függvény alapján.
   */
  // @Input() rowClass: RowClassFn;

  /**
   * Ki legyen-e jelölve egy adott sor.
   */
  // @Input() rowSelected: RowSelectedFn;

  // Outputs:

  @Output() cellClick = new EventEmitter<CellClickEvent>();

  // Properties:

  /**
   * Adatok objektumban.
   */
  gridView: GridDataResult;

  /**
   * Állapot.
   */
  state: State = {
    skip: 0,
    // take is removed: take: this.pageSize,
  };

  // Elements:

  /**
   * Grid elem.
   */
  @ViewChild(GridComponent) grid;

  /**
   * Kijelölő oszlop.
   */
  @ViewChild('checkboxColumn') checkboxColumn;

  /**
   * Megadott szlopok.
   */
  @ContentChildren(ColumnBase) columns: QueryList<ColumnBase>;

  /**
   * Megadott toolbar template.
   */
  @ContentChild(ToolbarTemplateDirective) toolbarTemplate;

  /**
   * Megadott detail template.
   */
  @ContentChild(DetailTemplateDirective) detailTemplate;

  constructor(
    private httpClient: HttpClient,
  ) {}

  ngOnInit() {
    // Első adatbetöltés:
    this.dataStateChange(this.state);
  }

  ngAfterViewInit() {

    // Ha meg vannak adva oszlopok, beállítjuk őket:
    if (this.columns && this.columns.length > 0) {

      // columns:
      const columnsToRender = [...this.columns.toArray()];

      // selectable:
      if (this.selectable && this.checkboxColumn) {
        columnsToRender.unshift(this.checkboxColumn);
      }

      this.columns.reset(columnsToRender);
      this.grid.columns = this.columns;
    }

    // Ha van detail template, beállítjuk:
    if (this.detailTemplate) {
      this.grid.detailTemplate = this.detailTemplate;
    }

    // Ha van toolbar template, beállítjuk:
    if (this.toolbarTemplate) {
      this.grid.toolbarTemplate = this.toolbarTemplate;
    }

    // "Újratöltjük" a gridet:
    this.grid.ngAfterContentInit();

  }

  /**
   * Adatok betöltése az állapot alapján.
   */
  dataStateChange(state: DataStateChangeEvent | State) {
    this.loadData().subscribe(data => {
      const newState = Object.assign({}, state, { take: this.pageSize });
      this.gridView = process(data, newState);
      this.state = newState;
      console.log(this.state);
    });
  }

  /**
   * Adatok lekérése.
   */
  loadData() {
    // Ha van dataUrl megadva, lekérjük a szervertől az adatokat. Ellenkező esetben a data propertyt használjuk.
    if (this.dataUrl) {
      return this.httpClient.get<any[]>(this.dataUrl, {
        params: new HttpParams().append('state', JSON.stringify(this.state))
      });
    } else {
      return Observable.of(this.data);
    }
  }

  private event(eventType: string) {
    return (event) => {
      if (this[eventType] instanceof EventEmitter) {
        this[eventType].emit(event);
      }
    };
  }

}
